
import XHeader from 'vux/src/components/x-header/index.vue'
import Drawer from 'vux/src/components/drawer/index.vue'
import PopupHeader from 'vux/src/components/popup-header/index.vue'
import XCircle from 'vux/src/components/x-circle/index.vue'
import Range from 'vux/src/components/range/index.vue'
import Icon from 'vux/src/components/icon/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Actionsheet from 'vux/src/components/actionsheet/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import Masker from 'vux/src/components/masker/index.vue'
import Box from 'vux/src/components/box/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Card from 'vux/src/components/card/index.vue'
import numberComma from 'vux/src/tools/number/comma.js'
import Swiper from 'vux/src/components/swiper/swiper.vue'

import {httpUrl} from '../../http_url';
import myDrawer from './Left.vue';
import SwiperItem from "../../../node_modules/vux/src/components/swiper/swiper-item.vue";

export default {
directives: {
TransferDom
},
data () {
return {
menus: {
menu1: '退出登录'
},
messagesNum:'0',
name:'',
phone:'',
showMenus: false,
msg_type:'',
surplus_amount: 0,
is_bank:'',
bankUrl:'',
weixin_status:'',
openUnbind_show:false,
on_order:'',          //是否又账单
bill_list:[],
bill_index:0,
duration:1000

}
},
created(){
this.getData();
this.getfenqiInfo();
this.getMessage();
},
components: {
SwiperItem,
XHeader,
Drawer,
PopupHeader,
XCircle,
Range,
Icon,
Actionsheet,
Group,
Cell,
CellBox,
XInput,
XButton,
Masker,
Box,
Grid,
GridItem,
Badge,
Card,
myDrawer,
Swiper
},
computed: {

amountPercent: function () {
return (this.surplus_amount)/2000;       
}
},
methods:{
getData(){
const _this = this;
this.$axios({
method: 'post',
url:httpUrl.homePage,
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.name = res.data.retData.name;
this.phone = res.data.retData.phone;
this.msg_type = res.data.retData.msg_type;
this.surplus_amount = res.data.retData.surplus_amount;
this.is_bank = res.data.retData.is_bank;
this.bankUrl = res.data.retData.bank_url;
this.weixin_status = res.data.retData.weixin_status;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
getfenqiInfo(){
const _this = this;
this.$axios({
method: 'post',
url:httpUrl.fenqiInfo,
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.on_order = res.data.retData.on_order;
this.bill_list = res.data.retData.list;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
getMessage(){
const _this = this;
this.$axios({
method: 'get',
url:httpUrl.messageList,
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.messagesNum = res.data.retData.count;
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}else if(res.data.errCode==401){
this.$vux.alert.show({
title: '错误提示',
content: '未登录，请先登录',
onHide () {
_this.$router.push('/quick_login');
}
})
}
}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goMessageList(){             //清除消息数字
this.$axios({
method: 'post',
url:httpUrl.updatemessage,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
this.$router.push('/message_list');
})

},
onIndexChange(index){
this.bill_index = index
},
golast(){
this.bill_index = this.bill_index-1
},
gonext(){
this.bill_index = this.bill_index+1
},
goBank(){
if(this.is_bank == 0){
this.$router.push('/bank_certify');
}else {

}
},
goEdu(){
this.$router.push('/quotas');
},
LoginOut(){
this.$axios({
method: 'post',
url:httpUrl.loginOut,
headers: {
'Content-Type': 'application/x-www-form-urlencoded;charset=UTF-8'
},
}).then((res)=>{
console.log(res.data)
if(res.data.errCode==200){
this.$router.push('/quick_login');
}else if(res.data.errCode==201){
this.$vux.toast.show({
type:'warn',
text: res.data.retMsg
})
}

}).catch(function () {
this.$vux.toast.show({
text: '网络异常，请稍后再试',
type:'warn'
})
});
},
goCreditInfoList(){
this.$router.push('/credit_info_list');
},
goEditPhone(){
if(this.msg_type=='2'){

}else {
this.$router.push({
path:'/edit_phone',
query: {
phone:this.phone
}
});
}
},



































showDrawer() {
this.$store.dispatch('changeDrawer').then(() => {

})
}
}
}



